import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProfitsComponent } from './profits.component';
import { AddProfitComponent } from './add-profit/add-profit.component';

const routes: Routes = [
  { path: '', component: ProfitsComponent },
  { path: 'add', component: AddProfitComponent },
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ProfitsRoutingModule {}
