import { Component, OnInit, TemplateRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { ProfitsService } from './profits.service';
import { ProductsService } from '../products//products.service';
import { CategoriesService } from '../categories/categories.service';
import { StoresService } from '../stores/stores.service';

import {
  ExportAsService,
  ExportAsConfig,
  SupportedExtensions,
} from 'ngx-export-as';

@Component({
  selector: 'app-profits',
  templateUrl: './profits.component.html',
  styleUrls: ['./profits.component.scss'],
})
export class ProfitsComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  constructor(
    private profitService: ProfitsService,
    private categoriesService: CategoriesService,
    private productsService: ProductsService,
    private storesService: StoresService,
    private modalService: BsModalService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private exportAsService: ExportAsService,
    route: ActivatedRoute
  ) {
    route.params.subscribe((val) => {
      this.getData();
    });
  }

  getData() {
    this.profitService.getAll();

    this.spinner.show();

    this.profitService.profits.subscribe(async (data) => {
      if (data) {
        this.items = data;

        console.log(data);

        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });

    this.spinner.hide();
  }
  ngOnInit(): void {
    // this.profitService.getAll();
    // this.spinner.show();
    // this.profitService.profits.subscribe(async (data) => {
    //   if (data) {
    //     this.items = data;
    //     if ( data.length < this.limits[0] - 1) {
    //       this.limit = data.length + 1;
    //       this.limits[0] = data.length + 1;
    //     }
    //     this.itemResource = new DataTableResource(data);
    //     this.itemCount = await this.itemResource.count();
    //     this.reloadItems({
    //       offset: 0,
    //       limit: this.limit,
    //     });
    //   }
    // });
    // this.spinner.hide();
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  deleteElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.profitService.deleteOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.profitService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  exportAsConfig: ExportAsConfig = {
    type: 'pdf',
    elementIdOrContent: 'my-table',
    options: {
      jsPDF: {
        orientation: 'landscape',
      },
    },
  };
  exportAs(type: SupportedExtensions, opt?: string) {
    this.exportAsConfig.type = type;
    if (opt) {
      this.exportAsConfig.options.jsPDF.orientation = opt;
    }
    this.exportAsService
      .save(this.exportAsConfig, 'Profits')
      .subscribe(() => {
        // save started
      });
    this.exportAsService.get(this.exportAsConfig).subscribe((content) => {
      console.log(content);
    });
  }
}
