import { Component, AfterViewInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

import { ProfitsService } from '../profits.service';
import { ProductsService } from '../../products//products.service';
import { CategoriesService } from '../../categories/categories.service';
import { StoresService } from '../../stores/stores.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

@Component({
  selector: 'app-add-profit',
  templateUrl: './add-profit.component.html',
  styleUrls: ['./add-profit.component.scss'],
})
export class AddProfitComponent implements AfterViewInit {
  constructor(
    private fb: FormBuilder,
    private profitsService: ProfitsService,
    private categoriesService: CategoriesService,
    private productsService: ProductsService,
    private storesService: StoresService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {}

  form = this.fb.group({
    profitType: [''],
    profitValue: [0],
    profitPercentage: [0],
  });

  ngAfterViewInit(): void {
    this.getProductsList();
    this.getbaseCategoriesList();
    this.getStoresList();
  }

  profitOnList: InputSelectItem[] = [
    { _id: '1', nameEn: 'Product', nameAr: '' },
    { _id: '2', nameEn: 'Category', nameAr: '' },
    { _id: '3', nameEn: 'Store', nameAr: '' },
  ];

  profitOn;
  products = [];
  categories = [];
  stores = [];
  selectProfitOn(event) {
    if (event) {
      console.log(event);
      this.profitOn = event.nameEn;
      if (this.profitOn === 'Product') {
        this.selectedStoreId = undefined;
      } else if (this.profitOn === 'Category') {
        this.selectedProductId = undefined;
      } else if (this.profitOn === 'Store') {
        this.selectedProductId = undefined;
      }
    }
  }
  ///////////////////////////////
  // Product ///////////////////
  selectProductsList: InputSelectItem[];
  selectedProductId: String;
  getProductsList() {
    console.log('getting products');

    this.spinner.show();

    this.productsService.getAll();

    this.productsService.products.subscribe(async (data) => {
      if (data) {
        this.selectProductsList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: '',
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectProduct(event) {
    if (event) {
      console.log(event);
      this.selectedProductId = event._id;
    }
  }
  // End Product
  //////////////////////////////////

  ///////////////////////////////
  // Store ///////////////////
  selectStoresList: InputSelectItem[];
  selectedStoreId: String;
  getStoresList() {
    console.log('getting stores');

    this.spinner.show();

    this.storesService.getAll();

    this.storesService.stores.subscribe(async (data) => {
      if (data) {
        this.selectStoresList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: '',
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectStore(event) {
    if (event) {
      console.log(event);
      this.selectedStoreId = event._id;
    }
  }
  // End Product
  //////////////////////////////////

  ///////////////////////////////
  // Category ///////////////////
  // Start select dropdown
  selectBaseCategoriesList: InputSelectItem[];
  subCategories = [];
  selectedCategory = {};
  getbaseCategoriesList() {
    console.log('getting categories');

    this.spinner.show();

    this.categoriesService.getBaseCategories();

    this.categoriesService.baseCategories.subscribe((data) => {
      if (data) {
        this.selectBaseCategoriesList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']['en']}`,
            nameAr: `${x['name']['ar']}`,
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectBaseCategory(event) {
    this.spinner.show();

    if (event) {
      this.selectedCategory = {};
      this.selectedCategory[0] = event;

      this.categoriesService.getOne(event._id).subscribe(
        (res) => {
          this.toastr.info(res['message'], 'Info');
          this.subCategories = [];
          if (res['data'].categories) {
            if (res['data'].categories.length > 0) {
              this.subCategories[0] = res['data'].categories.map((x) => {
                return {
                  _id: x['_id'],
                  nameEn: `${x['name']['en']}`,
                  nameAr: `${x['name']['ar']}`,
                };
              });
            }
          }
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
    }

    this.spinner.hide();
  }
  // End select dropdown

  selectSubCategory(fieldIndex, event) {
    this.spinner.show();

    // delete all next selected categories to index
    for (const key in this.selectedCategory) {
      if (key > fieldIndex + 1) {
        delete this.selectedCategory[key];
      }
    }

    // delete all next subcategories
    this.subCategories.splice(fieldIndex + 1);

    if (event) {
      // selected category of index = event
      this.selectedCategory[fieldIndex + 1] = event;

      this.categoriesService.getOne(event._id).subscribe(
        (res) => {
          this.toastr.info(res['message'], 'Info');
          if (res['data'].categories) {
            if (res['data'].categories.length > 0) {
              this.subCategories[fieldIndex + 1] = res['data'].categories.map(
                (x) => {
                  return {
                    _id: x['_id'],
                    nameEn: `${x['name']['en']}`,
                    nameAr: `${x['name']['ar']}`,
                  };
                }
              );
            }
          }
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );
    } else {
      // selected category of index = event
      delete this.selectedCategory[fieldIndex + 1];
    }

    this.spinner.hide();
  }
  // End Category
  //////////////////////////////////

  addStoreProfit() {
    if (!this.selectedStoreId || this.selectedStoreId === '') {
      return this.toastr.error('Select Store', 'Error');
    }
    this.spinner.show();

    this.storesService
      .addProfit(this.form.value, this.selectedStoreId)
      .subscribe(
        (res) => {
          this.toastr.info(res['message'], 'Info');
          this.router.navigate(['/profits']);
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );

    this.spinner.hide();
  }

  addCategoryProfit() {
    this.spinner.show();

    let selectedCategoryId;

    if (Object.keys(this.selectedCategory).length !== 0) {
      var count = 0;
      for (var prop in this.selectedCategory) {
        if (this.selectedCategory.hasOwnProperty(prop)) ++count;
      }
      if (this.selectedCategory[count - 1]) {
        selectedCategoryId = this.selectedCategory[count - 1]._id;
      }
    } else {
      return this.toastr.error('Select Category', 'Error');
    }

    let newForm = { ...this.form.value, storeId: this.selectedStoreId };
    this.categoriesService.addProfit(newForm, selectedCategoryId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['/profits']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  onSubmit() {
    if (this.profitOn === 'Store') {
      this.addStoreProfit();
    } else if (this.profitOn === 'Category') {
      this.addCategoryProfit();
    } else if (this.profitOn === 'Product') {
    }
  }
}
